import React, {Fragment, useEffect, useState} from 'react'
import {
    Cell,
    Footer,
    ScreenSpinner,
    Panel,
    PanelHeader,
    PanelHeaderBack,
    Placeholder,
    Input,
    Div
} from "@vkontakte/vkui";
import Icon56ArticleOutline from "@vkontakte/icons/dist/56/article_outline";

const GetCityList = ({ id, go, goBack, setItem, setPopout }) => {
    const [arrCities, setArrCities] = useState([]); //города для доставки
    const [nameCity, setNameCity] = useState('');
    useEffect(() => {
        //sahpossum.herokuapp.com
        const url = nameCity !== '' ? 'https://sahpossum.herokuapp.com/items/getCities/'+nameCity : 'https://sahpossum.herokuapp.com/items/getCities/1';
        fetch(url)
            .then(res => res.json())
            .then(res => {
                setArrCities(res);
            })
            .catch(e => console.log(e))
    }, [nameCity]);

    if (arrCities.length === 0) {
        return (
            <Panel id={id}>
                <PanelHeader left={<PanelHeaderBack onClick={() => goBack}/>}/>
                <Div>
                    <Input value={nameCity} onChange={(e)=>setNameCity(e.target.value)} />
                </Div>
                <Placeholder
                    stretched
                    icon={<Icon56ArticleOutline/>}
                    header={'Введите полное название города'}
                />
                <Footer/>
            </Panel>
        )
    } else {
        return (
            <Panel id={id}>
                <PanelHeader left={<PanelHeaderBack onClick={() => go('home')}/>}/>
                <Div>
                    <Input value={nameCity} onChange={(e)=>setNameCity(e.target.value)} />
                </Div>
                {
                    arrCities.map(item => {
                        return (
                            <Cell
                                multiline
                                key={item.id}
                                onClick={() => setItem(item)}
                                description={item.region || ''}
                            >{item.name}</Cell>
                        )
                    })
                }
                <Footer/>
            </Panel>
        )
    }
}

export default GetCityList